
.user-view{
	border-radius: 10px;
	box-shadow: 20px 20px 50px #000;
	margin-top: 150px;
    height: 400px;
    background: black !important;
    color: white;
   
}

.user-col{
    width: 50%;
}
.user-title {
    color: white !important;
	padding-top: 3%;
    padding-bottom: 10%;
	font-size: 2em;
    display: flex;
    font-weight: bold;
    justify-content: center;
	width: 100%;
}


.profile-label{
	font-size: 1.2rem;
    padding-top: 2%;
    font-weight: bold;
    margin-left: 35%;
}


.profile-value{
	font-size: 1.2rem;
    font-weight: normal;
    margin-left: 0;
      padding-top: 2%;
}

.back-button{
    width: 200px;
}
